import React from 'react';
import { Container, Typography, Box } from '@mui/material';
import Header from './Header1';
import Footer from './Footer';

const PoliticaDeEnvio = () => {
  return (
    <>
      <Header />
      <Container sx={{ mt: 4 }}>
        <Typography variant="h4" sx={{ mb: 2, fontWeight: 'bold' }}>Poltica de Envo</Typography>
        <Box sx={{ mb: 4 }}>
          <Typography variant="h6" sx={{ mb: 2 }}>Tiempo de Procesamiento de Envo</Typography>
          <Typography variant="body1" sx={{ mb: 2 }}>
            Todos los pedidos se procesan dentro de 2-3 das hbiles. Los pedidos no se envan ni se entregan los fines de semana o das festivos.
          </Typography>

          <Typography variant="h6" sx={{ mb: 2 }}>Tarifas de Envo y Estimaciones de Entrega</Typography>
          <Typography variant="body1" sx={{ mb: 2 }}>
            Ofrecemos dos mtodos de envo:
            <ul>
              <li>Econmico Areo: S/10.00 - 5 a 7 das hbiles</li>
              <li>Envo prioritario: S/17.00  - 2 a 3 das hbiles</li>
            </ul>
          </Typography>

          <Typography variant="h6" sx={{ mb: 2 }}>Confirmacin de Envo y Seguimiento de Pedido</Typography>
          <Typography variant="body1" sx={{ mb: 2 }}>
            Recibirs un correo electrnico de confirmacin de envo una vez que tu pedido haya sido enviado con tu(s) nmero(s) de seguimiento. El nmero de seguimiento estar activo en 24 horas.
          </Typography>

          <Typography variant="h6" sx={{ mb: 2 }}>Daos</Typography>
          <Typography variant="body1" sx={{ mb: 2 }}>
            ALDO'S MARKET no es responsable de ningn producto daado o perdido durante el envo. Si recibiste tu pedido daado, por favor contacta al transportista para presentar una reclamacin.
          </Typography>
        </Box>
      </Container>
      <Footer />
    </>
  );
};

export default PoliticaDeEnvio;
